* {
    box-sizing: border-box;
}
*:before,
*:after {
    box-sizing: border-box;
}
html{
    font-size: 62.5%;
    height: 100%;
    box-sizing: border-box;
}

body {
    font-family: Helvetica,Arial,sans-serif;
    font-size: $m-size;
    height: 100%;
    width: 100%;
    margin: 0px;
    padding: 0px;
    // overflow: hidden;
}
button {
    cursor: pointer;
}

button:disabled {
    cursor: default;
}


